import React, {useEffect,useContext,useState} from 'react';
import { UserContext } from "../../../../contexts/userContext";
import GamePlayer from '../GamePlayer/gamePlayer';
import config from "../../../../config"
import io from "socket.io-client";
import { useToasts } from 'react-toast-notifications'
import { motion } from "framer-motion"
import loading from "../tenor.gif"
const containerVariants = {
    hidden :{
      opacity : 0,
    },
    visible : {
      opacity : 1,
      transition:{
        delay : 0.5,
        duration : 1.5,
        stiffness : 50,
        ease : "easeInOut"
      }
    },
    exit: {
      y: '-100vh',
      transition : {
        ease: "easeInOut"
      }
    }
  }

const SinglePlayerGame = (props) => {
    let Socket = null
    let gamePath = props.location.pathname.match(/.*\/(.*)$/)[1];
    const gameUrl = config.SINGLEPLAYER_GAME_URL
    const {user, setUserInfo} = useContext(UserContext)
    const [ready, setReady] = useState(false)
    const [isDuplicate, setIsDuplicate] = useState(false)
    const { addToast } = useToasts()
    useEffect(() =>{
      console.log(gamePath)
      Socket = io(config.API_URL); 
      if(Socket){
        Socket.on("connect", () => {
            console.log("connetion")
            Socket.emit("ReactConnected",{userId : user.id, gameName : gamePath})
      });

        Socket.on("gameReady" , () =>{
          setTimeout(() =>{
            setReady(true)
          },3000)
        })

        Socket.on("isDuplicate" , () =>{
            setIsDuplicate(true)
            setTimeout(() =>{
              setReady(true)
            },3000)
           
          })

        Socket.on("challengeCompleted" , (challenge) =>{
            user.completedChallenges.push(challenge)
            user.tickets += challenge.reward;
            setUserInfo(user)
            addToast("good job you completed challenge: " + challenge.challengeName, { appearance: 'info' })
        })

      return () =>{
          Socket.close()
      }
  }

  },[])
    return(
        <motion.div className="container" variants={containerVariants} initial="hidden" animate="visible" exit="exit">
        {ready ? 
            isDuplicate ?
            <div variants={containerVariants} initial="hidden" animate="visible">Looks Like you have this open In another Tab</div>
            :
             <GamePlayer game={`${gameUrl}/${gamePath}/?${user.id}`}/>
            :
            <>
            <div 
               variants={containerVariants} initial="hidden" animate="visible"
            ><img src={loading} className="loading"/></div>
            {/* <BouncingLoader/> */}
            </>
        }
        </motion.div>
    )
 
}
 
export default SinglePlayerGame;